/***** Container surrounding Input Field *****/
.formContainer {
	width: 700px;
	
	/* Gradient taken from: https://uigradients.com/ */
	background: #00F260;                                              /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #0575E6, #00F260);          /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	transition: all 1.05s ease-in-out;
}

.formContainer:hover {   
 transform: scale(1.05, 1.05);
}
