/********************************* Detect Button *********************************/

/* Detect Button - Desktop */
button {
  cursor: pointer;
}

.detectButton {
	width: 30%;
	border-style: none;
	border-radius:0 5px 5px 0;
	color: white;
	background-color: purple;
}

.detectButton span {
  position: relative;
  transition: 0.5s;
}

.detectButton:hover span {
  padding-right: 25px;
}

.detectButton:hover span:after {
  opacity: 1;
  right: 0;
}

.detectButton span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

/* Detect Button - Mobile */
.detectMobileButton {
	width: 30%;
	border-style: none;
	border-radius:5px 5px 5px 5px;
	color: white;
	background-color: purple;
}

.detectMobileButton span {
  position: relative;
  transition: 0.5s;
}

.detectMobileButton:hover span {
  padding-right: 25px;
}

.detectMobileButton:hover span:after {
  opacity: 1;
  right: 0;
}

.detectMobileButton span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
